/* @import "../node_modules/bootstrap/scss/bootstrap.scss";
@import url('https://fonts.googleapis.com/css2?family=Honk&family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Nabla&display=swap'); */
@font-face {
    font-family:  'SF Pro Display Regular';
    src        :   url(../fonts/SanFranciscoFont-master/SanFranciscoDisplay-Regular.otf)
}
/* 
select everything */


/* global */
*
*::before ,
*::after  {
    margin  :  0;
    padding :  0;
}


body{
    margin: 0;
}

html {
    font-size   :  10px;
    font-family :  "SF Pro Display",  sans-serif;
}


a {
    display: block;
    text-decoration: none;
}

.container{
    max-width: 98rem;
    margin: 0 auto;
    padding: 0 2.2rem;
}


/* header */
header {
    position: fixed;
    top: 0;
    z-index: 1400;
    width: 100%;
    height: 4.4rem;
    background-color: rgba(0,0,0,0.8);
    backdrop-filter: blur(2rem);
}


.nav-list{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    margin: 0 -1rem;
}


.nav-list-mobile {
    display: none;
}


.nav-link {
    font-size : 1.4rem;
    color: #fff;
    padding: 0 1rem;
    transition: opacity .5s;
}


.nav-link:hover{
    opacity: 0.7;
}


.nav-link-apple{
    width: 1.6rem;
    height: 4.4rem;
    background: url("../assets/icons/apple.svg") center no-repeat
}


.nav-link-search{
    width: 1.6rem;
    height: 4.4rem;
    background: url("../assets/icons/search.svg") center no-repeat
}


.nav-link-bag {
    width: 1.4rem;
    height: 4.4rem;
    background: url("../assets/icons/bag.svg") center no-repeat
    
}





/* iPhone 11 Pro */
.iphone-11-pro {
    color: white;
    background: url("../assets/hero/iphone-11-pro/iphone_11_pro_large.jpg");
    /* margin-top: 4.4rem; */
}



.title {
    font-size: 5.6rem;
    line-height: 0.22;
    letter-spacing: -0.005rem;
    text-align: center;
}


.title-heading{
    font-size: 5.6rem;
    line-height: 1.07143;
    letter-spacing: -.005rem;
    height: 20px;
}



.title-sub-heading {
    font-size: 2.65rem;
    margin-top: 6.3rem;
    letter-spacing: 0.008rem;
    font-weight: 400;
}


.title-sub-heading-watch {
    font-size: 2.65rem;
    margin-top: -6.0rem;
    letter-spacing: 0.008rem;
    font-weight: 400;
    line-height: 30px;
}


.title-price {
    font-size: 1.7rem;
    color: #888;
    margin-top: .68rem;
}


.cta {
    text-align: center;
    margin-top: 2.4rem;
}


.cta-link {
    color: #66bbff;
    font-size: 2.1rem;
    display: inline-block;
}


.cta-link:nth-child(2){
    margin-left: 2.6rem;
}


.cta-link:hover{
    border-bottom: 2px solid #66bbff;
}




/* iPhone 11 */
.iphone-11{
    background: url("../assets/hero/iphone-11/iphone_11_large.jpg");
}


.title-dark{
    color: #000;
}


.cta-link-darker{
    color: #000;
}


.cta-link-darker:hover{
    border-bottom-color: #0070c9;
}


.watch{
    padding     : 25px;
    background  : url("../assets/hero/watch/watch_large.jpg");
}


.title-heading-watch{
    height: 7.8rem;
    background: url("../assets/hero/watch/logo_watch_large.png") center no-repeat;
    /* background-size: auto auto; */
    margin-top: 1.6rem;
}

/* DON'T TOUCH */
.hero  {
    height: 69.2rem;
    background-size: auto 73.6rem;
    background-position: center;
    background-repeat: no-repeat;
    border-top: .6rem solid #fff;
    border-bottom: .6rem solid #fff;
}
/*--------------*/


@media screen and (max-width: 1068px){
    .iphone-11-pro{
        background  :  url("../assets/hero/iphone-11-pro/iphone_11_pro_medium.jpg")
    }
    .iphone-11{
        background  :  url("../assets/hero/iphone-11/iphone_11_medium.jpg")
    }
    .watch{
        background  :  url("../assets/hero/watch/watch_medium.jpg")
    }

    .hero{
        height: 65rem;
        background-size: auto 69.8rem;
        background-position: bottom center;
        background-repeat: no-repeat;
    }

    .title-heading{
        font-size: 4.4rem;
    }


    .title-sub-heading{
        font-size: 2.4rem;
    }


    .cta-link:nth-child(2){
        margin-left: 2rem;
    }


    .title-heading-watch{
        height:6.2rem;
        background: url("../assets/hero/watch/logo_watch_medium.png") center no-repeat;
        background-size: auto 7.8rem;
    }
}



@media screen and (max-width:  767px){
    header{
        height  :   4.8rem;
        transition: 
            background .36s cubic-bezier(0.32, 0.08, 0.24, 1),
            height .56s cubic-bezier(0.32, 0.08, 0.24, 1),
    }


    header .container{
        padding: 0;
    }


    .iphone-11-pro {
        margin-top: 4.8rem ;
    }


    .nav-list-larger {
        display : none;
    }


    .nav-list{
        margin-top: 0;
    }


    .nav-list-mobile{
        display: flex;
    }

    .nav-item{
        width: 4.8rem;
        height: 4.8rem;
        display: flex;
        justify-content: center;
    
    }
}



